@media screen and (max-width: 1100px) {
  :root {
    --box-margin-x: 1rem;
    --box-margin-y: 1rem;
    --box-padding-x: 1rem;
    --box-padding-top: 1rem;
  }
}

@media screen and (max-height: 600px) {
  :root {
    --tooltip-title-font-size: 0.8rem;
    --tooltip-content-font-size: 0.7rem;
    --box-title-font-size: 1.2rem;
  }
}

.react-photo-album {
  img {
    cursor: pointer;
  }
}
